body {
  width: 100%;
  margin: 0;
}

.search-form {
  text-align: center;
  background-color: #2897ff;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-bottom: 15px;
  display: flex;
}

.search-input {
  width: 200px;
  height: 35px;
  border: 2px solid gray;
  border-radius: 5px;
  font-size: 18px;
}

.btn-submit {
  width: 40px;
  height: 35px;
  cursor: pointer;
  background-color: #fff;
  border: 2px solid gray;
  border-radius: 5px;
}

.hidden {
  display: none !important;
}

.btn {
  display: block;
}

.load-more {
  cursor: pointer;
  background-color: #2897ff;
  border-radius: 3px;
  margin: 20px auto;
  padding: 10px 20px;
  display: block;
}

